* {
  margin: 0;
  padding: 0;
  font-family: 'Merriweather', serif;
}

h1,h2,h3 {
  font-weight: 700;
}

canvas {
  display:none;
}

:root {
  background: white;
  --main-background: #fffcfa; 
  --main-fill: #51adfc;
  --second-fill: #91adc4;
  --main-stroke: black;
  --second-stroke: #477194;

  --ref-fill:#0281f0;
}

/* APP */

.App {
  background: var(--main-background);
}

.main-title {
  background: var(--main-fill);
  text-align: center;
  padding: 15px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-title h1 {
  font-size: 50px;
}

.main-content {
  background: var(--main-background);
  margin: auto;
  max-width: 850px;
  padding: 10px;
}

/* CONTENT */

.content-header {
  padding: 10px 0;
}

.content-header-subtitle {
  color: var(--second-stroke);
}

.content-paragraph {
  margin: 15px 0;
  line-height: 1.7;
}

.reference {
  display: block;
  margin: 10px 0px;
  text-indent: -30px;
	padding-left: 40px
}

.reference:before {
  content: "â€” ";
  color: var(--main-fill);
}

/* IMAGES */

.p-div {
  box-sizing: border-box;
  width: 40%;
  margin: 10px 30px;
}

#graf {
  display:none;
}

.p-div img {
  box-sizing: border-box;
  max-width: 100%;
}

.p-div span {
  box-sizing: border-box;
  font-size: 14px;
}

.float-left {
  float:left;
}

.float-right {
  float:right;
}

.img-block {
  margin: auto;
  text-align: center;
  width: 70%;
}

.img-block img {
  width: 100%;
  margin: 10px;
}

/* REFERENCES */

.ref-wrapper {
  display: inline;
}

.ref {
  color: var(--ref-fill);
  cursor: pointer;
}

.ref-content {
  position: absolute;
  width:70%;
  background: white;
  border: solid 2px var(--ref-fill);
  border-radius: 20px;
  padding: 15px;
  font-size: 14px;
  text-align: center;
  left: 50%;
  transform: translate(-50%,-50%);
}

/* BLOCKQUOTE */

blockquote {
  margin: 20px;
  position: relative;
  padding-left: 1em;
  border-left: 0.2em solid var(--main-fill);
  max-width: 60%;
  font-size: 20px;
  line-height: 2;
  font-weight: 100;
  text-align: left;
}

blockquote:after {
  content: "\201D";
}

cite {
  margin: 40px;
}

blockquote:before,
blockquote:after {
  content: "\201C";
  color: var(--main-fill);
}

blockquote:after {
  content: "\201D";
}

blockquote.no-quotes:before,
blockquote.no-quotes:after {
  content: none;
  color: var(--main-fill);
}

/* BUTTONS */

.default-button {
  box-sizing: border-box;
  width: 150px;
  color: black;
  background: var(--main-fill);
  border-radius: 20px;
  padding: 10px;
  margin: 0px 10px;
  cursor: pointer;
  border: none;
  font-size: 16px;
}

.default-button:hover {
  background: var(--second-fill);
}

.default-button:active {
  color: var(--main-fill);
  background: var(--main-stroke);
}

.default-button:focus {
  outline: 0;
}

.continue-button-div {
  display: flex;
  justify-content: flex-end;
  margin: 20px 10px;
}

/* FORM */
.form {
  box-sizing: border-box;
  margin: 30px auto;
  padding: 20px;
  border-radius: 10px;
  background: white;
  border: solid 1px;
}

.form-inputs {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.form-input {
  box-sizing: border-box;
  display: block;
  font-size: 16px;
  height: 40px;
  width: 80%;
}

.form-select {
  box-sizing: border-box;
  display: block;
  height: 40px;
  min-width: 80%;
  font-size: 16px;
}

.form-color {
  box-sizing: border-box;
  display: block;
  font-size: 50px;
  height: 50px;
  width: 50px;
  cursor: pointer;
}

.input-div {
  box-sizing: border-box;
  padding: 5px 0px;
}

.form-buttons-div {
  padding: 10px 0px;
  display: flex;
  justify-content: flex-end;
}

/* CHARTS */
.chart {
  text-align: center;
  margin: auto;
  padding: 10px;
}

.chart-svg {
  border: solid 1px black;
  background: white;
}

.chart-line {
  fill: none;
  stroke: red;
  stroke-width: 3px;
}

.axis-text {
  stroke: black
}

.chart-dot {
  fill: white;
  stroke: black;
}

.chart-dot-highlight {
  fill: var(--main-fill);
  stroke: black;
}

.chart-bar {
  fill:  var(--main-fill);
  stroke: black;
}

.chart-bar-highlight {
  fill: var(--main-fill);
  stroke: red;
  stroke-width: 4px;
}

.chart-cat {
  fill: white;
  stroke: black;
}

.chart-cat-highlight {
  fill: var(--main-fill);
  stroke: red;
  stroke-width: 3px;
}

.chart-tooltip text{
  font-size: 12px;
}

/* ADS */

.ad {
  text-align: center;
  margin: auto;
  padding: 10px;
}



/* FOOTER */
footer {
  border-top: 1px solid black;
  text-align: center;
  padding: 10px;
  font-size: 14px;
}